.nav {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 20px;
    height: 18.5px;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    transition: background-color 0.5s ease-in;
}

.nav__black {
    background-color: black;
}

.nav__logo {
    position: fixed;
    left: 50px;
    width: 80px;
    object-fit: contain;
    top: -0.1;
}

.nav__avatar {
    cursor: pointer;
    right: 70px;
    position: fixed;
    width: 30px;
    object-fit: contain;
}

.nav__notification {
    cursor: pointer;
    position: fixed;
    right: 110px;
    width: 32px;
    object-fit: contain;
    top: -0.1;
}

.nav__search {
    cursor: pointer;
    position: fixed;
    right: 155px;
    width: 20px;
    object-fit: contain;
    top: 21px;
}

.nav__links {
    display: flex;
    align-items: center;
}

.nav__link {
    margin-right: 20px;
    font-size: 16px;
    color: white;
    cursor: pointer;
    transition: color 0.3s ease-in;
    position: relative;
    left: 65%;
    transform: translateX(-50%);
    top: 10px;
    justify-content: space-between;
}

.nav__link:hover {
    color: #e50914;
}


@media (max-width: 768px) {
    .nav {
        padding: 10px;
        height: 14px; 
    }

    .nav__logo {
        width: 55px;
        left: 20px; 
    }

    .nav__avatar{
        width: 24px;
        right: 20px; 
    }

    .nav__notification,
    .nav__search{
        display: none;
    }

    .nav__link {
        display: none; 
    }
}
